.carts{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition:  all 0.5s ease-in-out;
    background-color: rgba(0, 0 ,0, .9);
    z-index: 999;
    padding-bottom: 5rem;
    visibility: hidden;
}

.cart1{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%)
    scale(.5);
            transform: translate(-50%, -50%)
    scale(.5);
    width: 167%;
    height: 157%;
    border-radius: 8px;
    background-color: #FFA500;
    overflow-x: scroll;
    transition: all .5s ease-in-out;
    
    opacity: 0;
    visibility: hidden;

}
.cart1> h2{
    text-align: center;
    font-size: 2rem;
}
.active1{
    visibility: visible;
}
.active2{
    -webkit-transform: translate(-50, -50%)
    scale(1.1);
            transform: translate(-50, -50%)
    scale(1.1);
    opacity: 1;
    visibility: visible;
    
}
.cart_Close> box-icon{
    display: inline-block;
    width: 50px;
    height: 50px;
    cursor: pointer;
    -webkit-transform: all .5s ease-in-out;
            transform: all .5s ease-in-out;
    margin: 3rem 0 0 2rem;
}
.cart_Close> box-icon:hover{
    fill: red;
}
